define("ember-formulaic/utils/slug", ["exports"], function (exports) {
    exports.generateSlug = generateSlug;
    /* global slug */

    function generateSlug(value) {
        if (value != null) {
            return slug(value, { lower: true });
        } else {
            return value;
        }
    }

    exports["default"] = {
        generateSlug: generateSlug
    };
});